import request from '../utils/request/js/index.js'
 var token = sessionStorage.getItem('token')
export default{
	 
	getUserInfo({urls,success, fail, complete} = {} ,canRunState = true ){
		console.log(urls)
		const instance = new request();
		const r = instance.get({
			url:'auth/wx_auth',
			data:{
				redirect_uri:urls,
			},
			header: {
				Token:token
			},
			success: res => {
			    success && success(res)
			},
			fail: err => {
			    fail && fail(err)
			},
			complete: res => {
			    complete && complete(res)
			}
		});
		//停止发送请求
		  !canRunState && instance.stop(r)
	}
	
}