export default{
	state:{
		hasLogin: false,
		openId: null,
		Token: null,
	},
	mutations:{
		setToken(state, provider) { //改变登录状态
		console.log(state)
		console.log(provider)
			state.hasLogin = true;
			state.Token = provider;
			sessionStorage.setItem('token',state.Token)
			// uni.setStorage({
			// 	key: 'userInfo',
			// 	data: provider
			// })
		
		},
	},
	actions:{
		getToken({commit},data){
			commit('setToken',data)
		}
	}
}